@import 'styles/shared';

body {
  margin: 0;
  background-color: $white;
  color: $black;
  line-height: 1.5;
  font-family: $primary-font-stack;

  background-color: teal;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 10px;
}

p {
  margin-top: 0;
}
